export default () => {
    self.addEventListener('message', e => { // eslint-disable-line no-restricted-globals
        if (!e) return;
        let data = e.data;
        console.log(data.model)
        if (data.action == "detect") {
            data.model.detect(data.source).then(predictions => {
                postMessage(predictions);
            })

        }
    })
}